@font-face {
  font-family: "Inter";
  src: url("../fonts/SourceSansPro-Regular.woff2") format("woff2"), url("../fonts/SourceSansPro-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Inter";
  src: url("../fontsSourceSansPror-SemiBold.woff2") format("woff2"), url("../fonts/SourceSansPro-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

.html {
  font-family: "SourceSansPro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  font-style: normal;

  margin: 0 auto;
}

.body {
  width: 100%;

  font-family: "SourceSansPro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20.11px;
  font-weight: 400;
  
  color: $text-color;
  background: $background-color;
  
  padding: 0;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-width: $desktop-width) {
    width: 1440px;
  }
}

.main {
  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    padding: 31px 32px 32px;
  } 
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;

  border: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;

  padding: 0;
  margin: -1px;
}

.hide {
  display: none;
}