.add-good {
  width: 100%;

  @media (min-width: $desktop-width) {
    width: 332px;

    padding: 1px 0 0;
  }
  
}

.add-good__title {
  text-align: center;

  @media (min-width: $desktop-width) {
    font-size: 28;
    line-height: 35.2px;
    font-weight: 600;
  
    margin: 0 0 16px;
  }
}

.add-good__form {
  text-align: center;

  @media (min-width: $desktop-width) {
    text-align: left;

    background: $input-background-color;

    box-shadow: 0 20px 30px rgba(0, 0, 0, 4%), 0 6px 10px rgba(0, 0, 0, 2%);

    border-radius: 4px;

    padding: 24px;
  }
}

.add-good__container {
  display: flex;
  flex-flow: column;

  border: none;
  padding: 0;
  margin: 0 0 16px;
  @media (min-width: $desktop-width) {
    border: none;
    padding: 0;
    margin: 0 0 16px;
  }
}

.add-good__label {
  width: max-content;

  font-size: 10px;
  line-height: 12.57px;
  letter-spacing: -2%;

  padding: 0;
  margin: 0 0 4px;
}

.add-good__label-good-name,
.add-good__label-good-image,
.add-good__label-good-price {
  position: relative;

  &::before {
    content: "";

    width: 4px;
    height: 4px;

    position: absolute;
    top: 0;
    right: -4px;

    border-radius: 50%;

    background: $input-required-background-color;
  }
}

.add-good__input {
  width: 284px;
  min-height: 36px;

  font-family: inherit;
  font-size: 12px;
  line-height: 15.08px;
  
  color: $input-color;
  background: $input-background-color;
  
  border: none;
  border-radius: 4px;
  
  box-shadow: 0 2px 5px rgba(0, 0, 0, 10%);
  
  padding: 10px 16px 11px;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.add-good__text-good-name,
.add-good__text-good-image,
.add-good__text-good-price {
  font-size: 8px;
  line-height: 10.06px;
  font-weight: 400;
  letter-spacing: -2%;

  color: $input-required-color;

  margin: 4px 0 0;
}

.add-good__textarea {
  width: 284px;
  min-height: 108px;

  color: $input-color;
  background: $input-background-color;

  border: none;
  border-radius: 4px;

  box-shadow: 0 2px 5px rgba(0, 0, 0, 10%);

  padding: 10px 16px 11px;
  margin: 0 auto;
  box-sizing: border-box;

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.add-good__input-good-push {
  width: 284px;
  min-height: 36px;

  text-align: center;

  color: $input-push-color;
  background: $input-push-background-color;

  border: none;
  border-radius: 10px;

  box-shadow: 0 2px 5px rgba(0, 0, 0, 10%);

  padding: 10px 16px 11px;
  box-sizing: border-box;
}

.add-good__input-good-push--active {
  color: $input-push-active-color;
  background: $input-push-active-background-color;
}