.catalog {
  width: 100%;

  @media (min-width: $desktop-width) {
    width: 1028px;
  }
}

.catalog__button-select {
  color: $input-color;
  background: $input-background-color;

  display: block;

  border: none;
  border-radius: 4px;

  box-shadow: 0 2px 5px rgba(0, 0, 0, 10%);

  padding: 10px 16px 11px;
  margin: 10px auto 16px auto;

  @media (min-width: $desktop-width) {
    margin: 0 0 16px auto;
  }
}

.catalog__list {
  list-style: none;

  display: flex;
  flex-flow: row wrap;
  gap: 16px;

  padding: 0;
  margin: 0;
}

.catalog__item {
  width: 100%;

  text-align: center;
  @media (min-width: $desktop-width) {
    width: 332px;

    text-align: left;

    position: relative;

    background: $catalog-item-background-color;

    box-shadow: 0 20px 30px rgba(0, 0, 0, 4%), 0 6px 10px rgba(0, 0, 0, 2%);

    border-radius: 4px;
  }
  

  &:hover .catalog__button-delete {
    display: block;
  }
}

.catalog__link {
  display: block;

  text-decoration: none;

  color: inherit;

  cursor: pointer;

  &::before {
    @media (min-width: $desktop-width) {
      content: "";

      position: absolute;
      inset: 0 0 0 0;
    }
  }

  &:focus-visible {
    @media (min-width: $desktop-width) {
      outline: none;
    }
  }

  &:focus-visible::before {
    @media (min-width: $desktop-width) {
      outline: 5px solid deepskyblue;
      outline-offset: -5px;
    }
  }
}

.catalog__link-container {
  width: 332px;
  height: 200px;
    
  position: relative;

  margin: 0 auto;
}

.catalog__link-image {
  width: 332px;
  height: 200;

  display: block;

  margin: 0 auto;

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.catalog__link-title {
  max-width: 300px;

  font-size: 20px;
  line-height: 25.14px;
  font-weight: 600;

  word-wrap: break-word;

  padding: 16px 16px 8px;
  margin: 0 auto;

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.catalog__link-description {
  padding: 8px 16px 0;
  margin: 0;
}

.catalog__link-price {
  font-size: 24px;
  line-height: 30px;
  font-weight: 600;

  padding: 32px 16px 16px;
  margin: 0;
}

.catalog__button-delete {
  width: 32px;
  height: 32px;

  display: none;

  position: absolute;
  top: -8px;
  right: -8px;

  background: $catalog-button-background-color;

  border: none;
  border-radius: 10px;

  box-shadow: 0 2px 4px rgba(0, 0, 0, 10%);

  padding: 0;
  margin: 0;

  cursor: pointer;
}

.catalog__button-image {
  width: 16px;
  height: 16px;
}